import Component from 'vue-class-component';
import Vue from 'vue';

const map: { [key: string]: string } = {
  'tag name duplicated': ''
};

@Component
export class TagHelper extends Vue {
  createTag(type:string) {
    let text = window.prompt('');
    if (!text) { return window.alert(''); }
    if(text.length > 4) { return window.alert(''); }
    text = text.substr(0, 4);
    this.$store.commit('createTag', {type: type, text});
    if (this.$store.state.createTagError) {
      window.alert(map[this.$store.state.createTagError.message]
        || '');
    } else {
      window.alert('');
    }
  }
}

export default TagHelper;